.CompanyPage_container__3UCJO {
  margin: auto;
  width: 60%;
  max-width: 800px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 2px 4px 10px rgba(0,0,0,.025);
  padding: 16px;
}

.CompanyPage_company-info-row__36ouJ {
  display: flex;
  justify-content: space-between;
}

.CompanyPage_container__3UCJO a {
  color: #344955;
  transition: color .2s;
}

.CompanyPage_container__3UCJO a:hover {
  color: #75b8e2;
}

.CompanyPage_fintime-container__1qZPZ {
  display: flex;
  margin: 0 20px;
}

.CompanyPage_fintime-button__mcblu {
  flex-grow: 1;
  height: 40px;
  line-height: 40px;
  background-color: transparent;
  transition: color .1s;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

.CompanyPage_fintime-button__mcblu:hover, :focus {
  color: #999;
}

.CompanyPage_fintime-button-active__2N528 {
  border-bottom: 4px solid #344955;
}

/* .fin-info {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  margin: 20px 20px 0 20px;
