.CompanyPage_fin-info__PDBqi {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 20px 0 20px;
}

.CompanyPage_fin-info__PDBqi span {
  margin-bottom: 16px;
  flex-basis: 20%;
}

.CompanyPage_fin-info__PDBqi span:nth-child(odd) {
  margin-bottom: 16px;
  flex-basis: 30%;
}

@media screen and (max-width: 620px) {
  .CompanyPage_container__3UCJO {
    width: 97%;
  }

  .CompanyPage_company-info-row__36ouJ {
    flex-direction: column;
  }

  .CompanyPage_company-info-row__36ouJ span {
    margin: 10px 0;
  }

  .CompanyPage_fintime-button__mcblu {
    font-size: 12px;
    font-weight: bold;
  }

  .CompanyPage_fin-info__PDBqi span {
    flex-basis: 30%;
  }

  .CompanyPage_fin-info__PDBqi span:nth-child(odd) {
    flex-basis: 70%;
  }
}
